	module.exports = {"data":{"markdownRemark":{"html":"<h2>SQL General Data Types</h2>\n<p>SQL general data types consist of the following: </p>\n<ol>\n<li>A text- and/or numeric-based value, often referred to as a STRING</li>\n<li>A numeric-only value, often referred to as an INTEGER</li>\n<li>A calendar- and/or clock-based value, often reffered to as DATE or TIME</li>\n<li>A database-specific value such as a boolean (two-option) flag, an array that stores multiple values within one data point</li>\n</ol>\n<p>More information on the specific data types in SQL can be found in the <a href=\"https://guide.freecodecamp.org/sql/sql-data-types/\">SQL Data Types</a> section of freeCodeCamp.</p>","fields":{"slug":"/sql/sql-general-data-types/"},"frontmatter":{"title":"SQL General Data Types"}}},"pathContext":{"slug":"/sql/sql-general-data-types/"}}

